@use '../util' as *;

.section_contact{
    margin-bottom: rem(75);
    .content__title{
        h1{font-weight: 100;}
        p{text-align: center;padding-bottom: rem(21);}
    }
    .contact{
        background: hsl(0, 0%, 91%);
        height: rem(573);
        label{
            font-size: rem(13);
            font-weight: 700;
        }
        input{
            border: none;
            padding: 0.8rem 1rem;
            border-radius: rem(3);
            font-weight: 100;
            font-size: rem(12);
            width: rem(305);
            margin-top: rem(16);
        }
        .contact1{
            &__subtitle{
                font-size: rem(20);
                font-weight: 600;
                padding-top: rem(27) ;
                padding-left: rem(32) ;
            }
            &__desc{
                padding-bottom: rem(24) ;
                padding-left: rem(32);
                font-size: rem(15);
                font-weight: 250;
            }
            &__form{
                padding-left: rem(32);    
                gap: rem(31);
            }
            &__content{
                display: flex;
                flex-direction: column;
                .drapeau{
                    flex-direction: row;    
                    margin-top: 1rem;
                    background-color: var(--white);
                    border-radius: 0.1875rem;
                    padding-left: 1rem;
                    input{
                        margin-top: 0;
                    }
                    img {
                        width: rem(22);
                        height: rem(22);
                        align-self: center;
                        border-right: rem(1) solid #979797;
                    }
                }
            }
            .message{
                grid-column: 1/3;
                textarea{
                    border: none;
                    border-radius: rem(3);
                    padding: rem(17);
                }
            }
        }
    }
}